li {
    list-style: none;
}
i {
    font-style:normal
}
label {
    font-weight: normal;
}
input[type="radio"],
input[type="checkbox"] {
    //vertical-align:middle;
    //margin:-2px 5px 0 0;
    margin: 2px 0 0;
}
.radio,
.checkbox {
    label {
        margin-right: 10px;
        &:last-child {
            margin-right: 0;
        }
    }
    span.error {
        display: none;
    }
}

.pipe {
    margin: 0 10px;
    color: lighten(@gray-base, 60%);
}
.text-red {
    color: @red;
}
.text-lightgray {
    color: lighten(@gray-base, 60%);
}

.pl-10 {
    padding-left: 10px;
}
.pd-0 {
    padding: 0 !important;
}
.ml-10 {
    margin-left: 10px;
}
.mt-10 {
    margin-top: 10px;
}
.mr-10 {
    margin-right: 10px;
}
.mr-5 {
    margin-right: 5px;
}
.mb-0 {
    margin-bottom: 0;
}
.mb-10 {
    margin-bottom: 10px;
}
.bd-0 {
    border: 0 !important;
}

.showStar:before {
    color: @red;
    vertical-align: top;
    content: "*";
}
