//
// flow
// --------------------------------------------------
// 
// notice
// --------------------------------------------------

.flow-dialog .modal-dialog {
    width: @flow-dialog-lg;
}

.flow-box {
    padding: 50px 0;
    &:extend(.clearfix all);

    li {
        a {
            padding: 20px 5px;
            display: block;
            text-decoration: none;
            border: 1px solid @gray-lighter;
            color: @gray;
            i {
                color: @gray-light;
            }
            span {
                margin-top: 15px;
                min-height: 34px;
                display: block;
            }
            &:hover,
            &.active {
                color: @white;
                border-color: @flow-bg-color;
                background-color: @flow-bg-color;
                i {
                    color: @white;
                }
            }
        }
    }
}

.flow-title {
    font-weight: 700;
    p {
        margin-bottom: 5px;
    }
}

.flow-group {
    margin-left: 10px;
    margin-right: 10px;
    &:extend(.clearfix all);

    ul {
        margin-bottom: 0;
        &.flow-box-left {
            width: 11.1%;
        }
        &.flow-box-right {
            width: 88.9%;
        }
    }
    p {
        margin-bottom: 5px;
    }
    h3 {
        margin: 5px 0 10px 0;
        font-weight: 700;
    }
    .flow-group-inline {
        margin-left: 20px;
        margin-bottom: 5px;
        &:extend(.clearfix all);
        .radio-inline, .checkbox-inline {
            padding-left: 0;
            margin-bottom: 5px;
        }
        .radio-inline + .radio-inline, .checkbox-inline + .checkbox-inline {
            margin-left: 0;
        }
    }
    textarea.form-control {
        width: 100%;
        border-radius: 0;
    }
    span.text-count{
        position:absolute;
        right: 6px;
        bottom: 6px;
        .text-count-word{
            color: @brand-danger;
            padding: 0 4px;
        }
    }
}
.flow-bline {
    margin-bottom: (@line-height-computed / 2);
    height: 0;
    border-bottom: 1px solid @gray-lighter;
    font-size: 0;
}
.flow-box-right {
  .flow-box-input{
      margin:0;
      padding:6px;
      border:none;
      position: absolute;
      left:1px;
      top:1px;
      z-index:10;
      width:75%;
      outline:none;
  }
    .flow-group-btn-group();
    .btn-group .dropdown-menu {
        padding: 0;
        &.dropdown-menu-static{
            padding:0 10px;
        }
        > li {
            padding: 5px 0;
        }
    }
}
.flow-group-btn-group {
    .btn-group {
        width: 100%;
        margin-top: 10px;
        &:first-child {
            margin-top: 0;
        }
        .btn[data-toggle="dropdown"],
        .dropdown-menu {
            width: 100%;
            border-radius: 0;
        }
        .btn {
            .box-shadow(none);
            &.btn-border{
                border-color:#bfbfbf;
                color:#333;
            }
            > span > i {
                color: @gray-light;
            }
        }
        .dropdown-menu {
            margin-top: -1px;
            padding: 5px;
            > li {
                padding: 5px 0 5px 5px;
                label {
                    padding: 2px 5px;
                    margin: 2px 10px;
                    border: 1px solid @gray-lighter;
                    input {
                        margin-right: 6px;
                    }
                }
              .menulist{
                padding:0px 0px;
                margin:0px 0px;
                background:none;
                border-bottom:none;
                .btn-group{
                  border-top:solid 1px #ffffff;
                  &.open{
                    .btn{
                      border-color:#83bde4;
                      border-bottom:none;
                      background:#f4f5f7;
                        z-index:1003;
                        color:@gray;
                    }
                  }
                  .btn{
                    text-align: left;
                    padding:6px;
                      background-color: transparent;
                      .caret{
                          margin-top: 7px;
                      }
                  }

                  .dropdown-menu{
                    min-width: 100%;
                    margin-top:-7px;
                    background:none;
                      top:42px;
                    border:none;
                      ul{
                          margin:0 0px;
                          background:#f4f5f7;
                          border:#83bde4 1px solid;
                          padding:0;
                          li{
                              label{
                                  padding:2px 0;
                                  margin:2px 0;
                              }
                          }
                      }
                  }
                }
              }
            }
        }
        &.active {
            > .btn {
                border-color: @brand-warning;
                > span > i {
                    color: @brand-warning;
                }
            }
        }
    }
}
.flow-group-menu {
    .flow-group-btn-group();
    textarea.form-control {
        width: 100%;
        border-radius: 0;
    }
    span.text-count{
        position:absolute;
        right: 6px;
        bottom: 6px;
        .text-count-word{
            color: @brand-danger;
            padding: 0 4px;
        }
    }
}

.flow-group-wrap {
    border: 1px solid @gray-lighter;
    padding: 10px;
    margin-top: 10px;

    .btn-group {
        width: 48.5%;
        margin-top: 0;
        display: inline-block;
        float: right;
        &:first-child {
            float: left;
        }
        .dropdown-menu {
            padding: 0;
            > li {
                label {
                    margin: 0;
                    input {
                        margin-right: 4px;
                    }
                }
            }
        }
    }
}

.flow-group-btn {
    margin-top: 10px;
    .btn {
        margin-left: 5px;
        margin-right: 5px;
    }
}

.flow-steps {
    width: 32px;
    position: absolute;
    top: 40%;
    right: 1px;
    border: 1px solid #e4e4e4;
    a {
        display: block;
        width: 32px;
        height: 44px;
        line-height: 17px;
        padding: 5px 0;
        background-color: @white;
        text-align: center;
        text-decoration: none;
        color: @gray;
        &.active {
            padding-top: 15px;
            background-color: @brand-success;
            i {
                font-size: 1.5rem;
                color: @white;
            }
        }
        &.next {
            background-color: @brand-primary;
            color: @white;
        }
    }
}
.dropdown-menu-static{
    li{
        position:relative;
        margin:0;
        padding: 0 6px;
        .static{
            position: static;
        }
    }
    .btn-group{
        position: static;
        .dropdown-menu{
            li{
                label{border:none;}
            }
        }

    }
}
