//
// Modals
// --------------------------------------------------

.modal-content {
  border-radius: @modal-border-radius;
}
.modal-header {
    background-color: @modal-header-bg-color;
}
.modal-title {
    font-size: @font-size-14;
}
.modal-header .close {
    opacity: .6;
    &:hover {
        opacity: .4;
    }
}
.modal-footer {
    .btn {
        min-width: @btn-sm-min-width;
    }
}
