// Grid
// --------------------------------------------------
// 
// 1200px100%
// --------------------------------------------------

.container {
  @media (min-width: @screen-sm-min) {
    .width-auto();
  }
  @media (min-width: @screen-md-min) {
    .width-auto();
  }
  @media (min-width: @screen-lg-min) {
    .width-auto();
  }
}

.width-auto {
  width: auto;
  max-width: @container-lg;
}

.sino-container {
    .sino-side {

    }
    .sino-main {
        @media (max-width: @screen-xs-max) {
            padding-left: 0;
        }
        .row {
            margin-left: 0;

            ul.nav-tabs-title:first-child {
                margin-top: 0;
            }
        }
        .col-sm-11.col-md-11 {
            width: 88.5%;
            @media (min-width: @screen-sm-min) {
                width: ~'-moz-calc(100% - 100px)';
                width: ~'-webkit-calc(100% - 100px)';
                width: ~'calc(100% - 100px)';
            }
        }
        .col-sm-1.col-md-1 {
            width: 100px;
        }
    }
}
